.payment-wrapper {
  padding: 2rem;
  width: 100%;
  display: flex;
  justify-content: center;
}

.payment-card-box {
  background-color: #fff;
  padding: 2rem 1.5rem;
  border-radius: 18px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  max-width: 600px;
  width: 100%;
}

/* Tabs */
.tab-active {
  background: #f2f6ff;
  border: none;
  border-bottom: 2px solid #2563eb;
  color: #2563eb;
  font-weight: 600;
  font-size: 1.1rem;
  padding: 0.75rem 0;
  border-radius: 12px 12px 0 0;
  transition: background 0.2s;
}
.tab-inactive {
  background: transparent;
  border: none;
  border-bottom: 2px solid transparent;
  color: #888;
  font-weight: 500;
  font-size: 1.1rem;
  padding: 0.75rem 0;
  border-radius: 12px 12px 0 0;
  transition: background 0.2s;
}
.tab-active:hover, .tab-inactive:hover {
  background: #e0e7ff;
  color: #1d4ed8;
}

/* Accordion */
.accordion-item {
  background: #f9fafb;
  border-radius: 14px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.04);
  margin-bottom: 1.1rem;
  overflow: hidden;
  border: 1px solid #e5e7eb;
  transition: box-shadow 0.2s, border 0.2s;
}
.accordion-item:hover {
  box-shadow: 0 6px 18px rgba(0,0,0,0.10);
  border: 1px solid #b6c3e6;
}
.accordion-header {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 1.1rem 1.2rem;
  background: #f9fafb;
  transition: background 0.2s;
  border-bottom: 1px solid #e5e7eb;
}
.accordion-header:hover {
  background: #f1f5fd;
}
.payment-icon {
  height: 38px;
  width: 38px;
  max-width: 38px;
  max-height: 38px;
  object-fit: contain;
  border-radius: 8px;
  background: #fff;
  box-shadow: 0 1px 4px rgba(0,0,0,0.06);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 18px;
}
.accordion-title {
  font-size: 1.08rem;
  font-weight: 600;
  color: #222;
  margin-bottom: 2px;
}
.accordion-desc {
  font-size: 0.97rem;
  color: #666;
  margin-bottom: 0;
}
.accordion-detail {
  background: #f4f7ff;
  border-top: 1px solid #e5e7eb;
  padding: 1.2rem 1.5rem;
  font-size: 1rem;
  color: #333;
  animation: fadeIn 0.3s;
  margin: 0;
  line-height: 1.6;
}
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: none; }
}

/* Responsive */
@media (max-width: 700px) {
  .payment-card-box {
    padding: 1rem 0.5rem;
    max-width: 98vw;
  }
  .accordion-header, .accordion-detail {
    padding-left: 0.7rem;
    padding-right: 0.7rem;
  }
}
