.sidebar {
  background: #fff;
  border-radius: 18px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  padding: 2rem 1.5rem;
  max-width: 340px;
  width: 100%;
  margin-right: 2rem;
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}
.title {
  font-size: 1.25rem;
  font-weight: 700;
  margin-bottom: 0.7rem;
  color: #2563eb;
  letter-spacing: 0.5px;
}
.selector {
  margin-bottom: 0;
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
}
.label {
  font-size: 0.98rem;
  font-weight: 500;
  color: #444;
  margin-bottom: 0.1rem;
  margin-left: 2px;
}
.searchBox {
  width: 100%;
  padding: 0.6rem 1rem;
  border-radius: 10px;
  border: 1px solid #e5e7eb;
  font-size: 1rem;
  margin-bottom: 0.1rem;
  background: #f4f7ff;
  transition: border 0.2s;
}
.searchBox:focus {
  border: 1.5px solid #2563eb;
  outline: none;
}
.list {
  background: #f9fafb;
  border-radius: 12px;
  box-shadow: 0 1px 4px rgba(0,0,0,0.04);
  padding: 0.2rem 0.2rem 0.2rem 0.2rem;
  margin-bottom: 0.5rem;
  max-height: 260px;
  overflow-y: auto;
  border: 1px solid #e5e7eb;
  margin-top: 0.1rem;
  width: 100%;
}
.listItem {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  min-width: 0;
  box-sizing: border-box;
  padding: 0.7rem 1rem;
  border-radius: 8px;
  font-size: 1.05rem;
  font-weight: 500;
  color: #222;
  margin-bottom: 2px;
  cursor: pointer;
  transition: background 0.18s, color 0.18s, box-shadow 0.18s;
  border: none;
  background: transparent;
  box-shadow: none;
}
.listItem:hover, .listItemActive {
  background: #e0f2fe;
  color: #2563eb;
  box-shadow: 0 2px 8px rgba(37,99,235,0.07);
}
.listItemIcon {
  width: 22px;
  height: 22px;
  margin-right: 10px;
  object-fit: contain;
  border-radius: 4px;
  background: #fff;
  box-shadow: 0 1px 2px rgba(0,0,0,0.04);
  flex-shrink: 0;
}
.listItemMain {
  display: flex;
  align-items: center;
  flex: 1 1 0;
  min-width: 0;
  overflow: hidden;
}
.listItemName {
  font-weight: 600;
  flex: 1 1 0;
  min-width: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.listItemRight {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  min-width: 70px;
  margin-left: 10px;
  flex-shrink: 0;
}
.listItemSub {
  color: #888;
  font-size: 0.93em;
  font-weight: 400;
  margin-bottom: 2px;
  margin-top: 0;
  text-align: right;
}
.listItemCount {
  color: #22c55e;
  font-weight: 700;
  font-size: 1rem;
  margin-left: 0;
  min-width: 60px;
  text-align: right;
}
.showAllBtn {
  display: block;
  width: 80%;
  margin: 0.5rem auto 0 auto;
  background: #f2f6ff;
  color: #2563eb;
  border: none;
  border-radius: 8px;
  font-weight: 600;
  font-size: 1rem;
  padding: 0.6rem 0;
  cursor: pointer;
  transition: background 0.18s, color 0.18s;
  text-align: center;
}
.showAllBtn:hover {
  background: #e0e7ff;
  color: #1d4ed8;
}
.button {
  width: 100%;
  margin-bottom: 0.3rem;
  padding: 0.7rem 1rem;
  border-radius: 8px;
  border: none;
  background: #e0f2fe;
  color: #2563eb;
  font-weight: 600;
  font-size: 1.05rem;
  cursor: pointer;
  transition: background 0.18s, color 0.18s;
}
.selectedButton {
  background: #2563eb;
  color: #fff;
}
@media (max-width: 900px) {
  .sidebar {
    max-width: 100vw;
    margin-right: 0;
    padding: 1rem 0.5rem;
  }
} 